.country_info_footer {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  gap: 20px;
}

.country_info_footer p {
  margin-top: 6px;
  width: 150px;
}

.country_info_footer ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, 80px);
  width: 100%;
  column-gap: 10px;
  row-gap: 5px;
  align-items: start;
}

.country_info_footer li {
  text-align: center;
  border: 1px solid black;
  padding: 5px 0;
  border-radius: 2.5px;
  background-color: var(--secondary-bg);
  border: none;
  box-shadow: var(--shadow);
}

.country_info_footer li a {
  text-decoration: none;
  color: var(--text-white);
  display: block;
  padding: 5px 0;
}

@media screen and (min-width: 1024px) {
  .country_info_footer {
    flex-direction: row;
  }
}
