.themeToggler {
  color: var(--text-white);
  font-size: 12.5px;
  display: flex;
  gap: 10px;
  border-radius: 10px;
  align-items: center;
  letter-spacing: 0.5px;
}

.themeToggler:hover {
  cursor: pointer;
}

.themeToggler svg {
  height: 10px;
  width: 10px;
}

p {
  font-size: 12px;
}
